  .buttonproceed {
    cursor: pointer;
    position: relative;
    text-align: center;
    display: block;
    width: 270px;
    border: none;
    font-size: 12px;
    height: 45px;
    text-decoration: none;
    font-weight: 600;
    overflow: hidden;
    box-shadow: 0 10px 50px #313234;
    border-radius: 20px;
  }
  
  
  
  .buttonproceed:hover .slidein {
    left: 0%;
  }
  
  
  
  
  
  .buttonproceed .slidein {
    background: #000000;
    left: -100%;
    z-index: 2;
  }
  
  .buttonproceed p {
    text-transform: uppercase;
    transition: left 300ms;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 1px;
    background: #000000;
    position: absolute;
    font-weight: bold;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    color: #fff;
  }
  
  .slidein:nth-child( even) {
    background: #ffffff !important;
    color: #000000;
  }
  
  .slidein:nth-child( odd) {
    background: #ffffff !important;
    color: #000000;
  }