.intro{
  
  

  position: absolute;
  
  top: 25%;
  
  left: 40%;
  
  transform: translate(-50%, -50%);
  
  font-size: 70px;
  
  -webkit-text-stroke: 1px rgb(255, 255, 255);
  
  color: transparent;
  
  background-image: linear-gradient(45deg,rgb(0, 0, 0), rgb(0, 0, 0));
  background-repeat: no-repeat;
  
  -webkit-background-clip: text;
  
  background-position: -330px 0; 
  animation: backcolor 7s linear infinite alternate;
  
  } @keyframes backcolor {
  
  100%{
  
  background-position: 0 0;
  
  
  
  }


}
.trip{
        position: absolute;
        top: 38%;
        left:50%;
        color:rgb(0, 0, 0);
        -webkit-text-stroke: .2px #fff;
        font-style: italic;
        font-size: 30px;
        overflow: hidden;
      }
      
      .vis
      {
        position: absolute;
        top: 88%;
        padding-left: 570px;
        -webkit-text-stroke: 1px #fff;
        
      }
      .hero{
        width: 100%;
        height: 100vh;
        overflow: hidden;
        background-color: black;
        
      }
      
      video{
        width:100%;
        height: 100vh;
        object-fit: cover;
      }
.overlay{
  position: absolute;
  height: 100%;
  width:100%;
  background-color: rgb(0, 0, 0,);
}


