.card {
    display: flex;
    position: relative;
    left: 200px;
    top:150px;
    justify-items: center;
    justify-content: center;
    width: 12rem;
    cursor: pointer;
  }
  
  .content {
    text-align: center;
    position: relative;
    -webkit-transition: all 2.25s;
    transition: all 2.25s;
    background-image: url("images/SingaporeFlag.jpg");
    background-size: cover;
    padding: 5em;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  }
  
  .card:hover .content {
    -webkit-transform: rotateY(0.5turn);
            transform: rotateY(0.5turn);
          
          }
          
  .front,
  .back {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 2em;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .title {
    -webkit-transform: translateZ(5rem);
    transform: translateZ(5rem);
    font-size: 2rem;
  }
  
  .subtitle {
    -webkit-transform: translateZ(2rem);
    transform: translateZ(2rem);
  }
  
  .back {
    -webkit-transform: rotateY(0.5turn);
    transform: rotateY(0.5turn);
    background-image: url("images/loginpage.jpg");
    background-size: cover;
  }
  
  .description {
    -webkit-transform: translateZ(3rem);
    transform: translateZ(3rem);
    
    color:rgb(0, 0, 0);
  } 
